.auth-form {
  box-sizing: border-box;
  max-width: 396px;
  width: 100%;
  margin: 0 auto;
  display: flex;
  flex-direction: column;
  justify-content: space-between;
}

.auth-form__title {
  box-sizing: border-box;
  max-width: 396px;
  width: 100%;
  margin: 0 auto;
  display: flex;
  flex-direction: column;
  padding: 70px 0 20px 0;
}

.auth-form__input-fieldset {
  flex: 1 266px;
  margin: 0;
  padding: 0;
  border: unset;
}

.auth-form__input-container {
  margin-bottom: 20px;
}

.auth-form__input-container:last-of-type {
  margin-bottom: 10px;
}

.auth-form__input-label {
  font-family: 'Inter', Arial, sans-serif;
  font-weight: normal;
  font-style: normal;
  font-size: 10px;
  line-height: 1.2;
  color: #A0A0A0;
  margin: 0;
  padding: 0;
  text-align: left;
  display: flex;
  flex-direction: column;
}

.auth-form__input {
  width: 100%;
  min-height: 46px;
  text-align: left;
  font-family: 'Inter', Arial, sans-serif;
  font-weight: normal;
  font-style: normal;
  font-size: 13px;
  line-height: 1.23;
  color: #000000;
  padding: 15px;
  margin-top: 10px;
  border: unset;
  box-sizing: border-box;
  background: #F9F9F9;
  border-radius: 8px;
}

.auth-form__input:invalid {
  color: #EE3465;
}

.auth-form__input-error {
  text-align: left;
  font-family: 'Inter', Arial, sans-serif;
  font-weight: normal;
  font-style: normal;
  font-size: 10px;
  line-height: 1.2;
  color: #EE3465;
}

.auth-form__button-container {
  box-sizing: border-box;
  display: flex;
  flex-direction: column;
  justify-content: flex-end;
  flex: 1 207px;
}

.auth-form__submit-button {
  font-family: 'Inter', Arial, sans-serif;
  font-weight: 500;
  font-style: normal;
  font-size: 14px;
  line-height: 1.21;
  text-align: center;
  width: 100%;
  margin: 0;
  padding: 15px 0;
  box-sizing: border-box;
  background: rgba(52, 86, 243, 1);
  border-radius: 3px;
  border: unset;
  color: rgb(255, 255, 255, 1);
  cursor: pointer;
  transition: background .3s ease;
}

.auth-form__submit-button:hover {
  background: rgba(52, 86, 243, .8);
}

.auth-form__submit-button:disabled {
  cursor: not-allowed;
}

@media screen and (max-width: 1023px) {
  .auth-form__title {
    padding-top: 232px;
  }

  .auth-form__button-container {
    flex-basis: 379px;
  }
}

@media screen and (max-width: 767px) {
  .auth-form__title {
    align-items: center;
    padding-top: 40px;
    padding-bottom: 30px;
  }

  .auth-form__button-container {
    justify-content: flex-end;
  }

  .auth-form__submit-button {
    font-size: 12px;
    line-height: 1.25;
  }

  .auth-form__button-container {
    flex-basis: 261px;
  }
}
